<template>
  <div>
    <a-button type="primary" @click="showAddCateBox">添加分类</a-button>
  </div>
    <a-table class="cat-table" :columns="columns" :data-source="cateList" rowKey="cat_id" :bordered="true" :pagination="false" style="margin:20px 0">
        <template #state="{text}">
              <CheckCircleTwoTone v-if="!text" twoToneColor="#52c41a" />
              <CloseCircleTwoTone v-else twoToneColor="#f00" />
        </template>
        <template #level="{text}">
              <a-tag color="green" v-if="text==0">一级</a-tag>
              <a-tag color="blue" v-else-if="text==1">二级</a-tag>
              <a-tag color="purple" v-else>三级</a-tag>
        </template>
        <template #handler="{text}">
          <a-button type="primary" size="small" @click="showCatEditBox(text.cat_id)">编辑</a-button>
          <a-button type="primary" danger size="small" @click="deleteCat(text.cat_id)">删除</a-button>
        </template>
    </a-table>
  <!-- 分页器 -->
        <a-config-provider :locale="locale">
      <a-pagination @change="pageChangeHandler" @showSizeChange="showSizeChangeHandler" :defaultPageSize="2" :pageSizeOptions="['1','2','5','10']" :total="total" show-size-changer show-quick-jumper />
    </a-config-provider>
    <!-- 添加分类的对话框组件 -->
      <a-modal v-model:visible="addCateVisible" title="添加分类" @ok="addCateHandleOk" @cancel="closeDialogHandler">
        <p>
          <a-form
            :model="addCateFormData"
            :rules="rules"
          >
          <a-form-item label="分类名称" name="cat_name">
            <a-input v-model:value="addCateFormData.cat_name" />
          </a-form-item>
          </a-form>
        </p>
      <p>选择父级分类:
          <a-cascader
          :options="allCateList"
          :display-render="displayRender"
          expand-trigger="hover"
          placeholder="请选择分类"
          :changeOnSelect="true"
          v-model:value="selectedValue"
          :fieldNames="{ label: 'cat_name', value: 'cat_id', children: 'children' }"
        />
      </p>
    </a-modal>
    <!-- 编辑分类对话框组件-->
    <a-modal v-model:visible="showEditBox" width="1000px" title="编辑分类" @ok="editCatHandle">
      <p><span>请输入分类名称 : </span><a-input v-model:value="editValue.cat_name"></a-input></p>
    </a-modal>
</template>

<script>
import zhCN from 'ant-design-vue/es/locale/zh_CN'
import { CheckCircleTwoTone, CloseCircleTwoTone } from '@ant-design/icons-vue'
import { getCurrentInstance, onMounted, reactive, ref } from 'vue'
import { message } from 'ant-design-vue'
export default {
  components: {
    CheckCircleTwoTone,
    CloseCircleTwoTone
  },
  setup () {
    // 获取VUE实例
    const { proxy } = getCurrentInstance()
    // 保存商品分类数据
    const cateList = ref([])
    // 渲染表格数据的规则
    const columns = [
      {
        title: '分类名称',
        dataIndex: 'cat_name'
      },
      {
        title: '分类ID',
        dataIndex: 'cat_id'
      },
      {
        title: '是否有效',
        dataIndex: 'cat_deleted',
        width: '10%',
        slots: { customRender: 'state' }
      },
      {
        title: '分类等级',
        width: '20%',
        dataIndex: 'cat_level',
        slots: { customRender: 'level' }
      },
      {
        title: '操作',
        slots: { customRender: 'handler' },
        width: '12%'
      }
    ]
    // 请求商品分类数据的参数
    const catParams = {
      type: 3,
      pagenum: 1,
      pagesize: 5
    }
    // 获取商品分类列表
    const total = ref()
    const queryCateList = async () => {
      const { meta, data } = await proxy.$http.get('categories', {
        params: catParams
      })
      total.value = data.total
      if (meta.status == 200) cateList.value = data.result
    }
    // 定义页码改变函数
    const pageChangeHandler = (page, pagesize) => {
      catParams.pagenum = page
      catParams.pagesize = pagesize
      queryCateList()
    }
    // 定义页码尺寸更改的触发函数
    const showSizeChangeHandler = (current, size) => {
      catParams.pagenum = current
      catParams.pagesize = size
      queryCateList()
    }
    onMounted(() => {
      queryCateList()
    })
    // 控制添加分类对话框显示
    const addCateVisible = ref(false)
    // 定义级联选择框数据源
    const allCateList = ref([])
    // 定义级联选择框数据
    const selectedValue = ref([])
    // 点击显示添加数据的盒子
    const showAddCateBox = async () => {
    // 请求所有商品分类数据
      const { meta, data } = await proxy.$http.get('categories', {
        params: {
          type: 2
        }
      })
      if (meta.status != 200) return message.error(meta.msg)
      allCateList.value = data
      addCateVisible.value = true
    }
    // 表单验证规则
    const rules = {
      cat_name: [
        { required: true, message: '请输入分类名称', trigger: 'blur' }
      ]
    }
    // 添加分类表单绑定的数据
    const addCateFormData = reactive({
      cat_name: ''
    })
    // 定义发送对象
    const addCateParams = {
      cat_pid: 0,
      cat_level: 0
    }
    // 把级联选择框里面选择的数据选择出来
    const displayRender = ({ labels, selectedOptions }) => {
    // 如果次参数长度不为0,说明选择了父分类
      if (selectedOptions.length != 0) {
        addCateParams.cat_pid = selectedOptions[selectedOptions.length - 1].cat_id
        addCateParams.cat_level = selectedOptions[selectedOptions.length - 1].cat_level + 1
      } else {
        addCateParams.cat_pid = 0
        addCateParams.cat_level = 0
      }
      return labels.join('/')
    }
    // 点击确定来发送添加分类的请求
    const addCateHandleOk = async () => {
      addCateParams.cat_name = addCateFormData.cat_name
      const { meta } = await proxy.$http.post('categories', addCateParams)
      addCateVisible.value = false
      if (meta.status != 201) return message.error(meta.msg)
      message.success(meta.msg)
      queryCateList()
      addCateFormData.cat_name = ''
      selectedValue.value = ''
    }
    // 点击取消关闭对话框并且清空对话框里面的数据
    const closeDialogHandler = () => {
      addCateFormData.cat_name = ''
      selectedValue.value = ''
    }
    // 删除商品
    const deleteCat = async id => {
      console.log(id)
      const { meta } = await proxy.$http.delete(`categories/${id}`)
      queryCateList()
      if (meta.status == 200) return message.success(meta.msg)
    }
    // 定义编辑的内容
    const editValue = reactive({
      cat_name: '',
      cat_id: ''
    })
    // 定义编辑盒子弹窗
    const showEditBox = ref(false)
    // 编辑商品
    const showCatEditBox = async id => {
      // 获取分类的信息
      const { data } = await proxy.$http.get('categories/' + id)
      editValue.cat_name = data.cat_name
      editValue.cat_id = data.cat_id
      showEditBox.value = true
    }

    // 编辑分类的提交
    const editCatHandle = async () => {
      const { meta } = await proxy.$http.put('categories/' + editValue.cat_id, editValue)
      if (meta.status != 200) return message.error(meta.msg)
      message.success(meta.msg)
      showEditBox.value = false
      editValue.cat_name = ''
      queryCateList()
    }
    return {
      addCateFormData,
      addCateVisible,
      showEditBox,
      editValue,
      editCatHandle,
      showCatEditBox,
      closeDialogHandler,
      pageChangeHandler,
      showSizeChangeHandler,
      addCateHandleOk,
      displayRender,
      deleteCat,
      showAddCateBox,
      selectedValue,
      allCateList,
      cateList,
      rules,
      total,
      locale: zhCN,
      columns
    }
  }
}
</script>

<style lang="less" scoped>

</style>
